import React, { Component } from 'react';
import {defaultUserPic,eosIcon,inviteIcon,shbIcon,stockIcon,rnbIcon,bookIcon,ruleIcon,wxIcon,ariIcon,languageIcon} from '../../public';
import "./menu.css";
import List from './list';
import Eos from '../eosRecarge/eosRecarge';
class Menu extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	a:1,
    	menuCallBack:props.callBack
    };
    
  }
	eosClick(){
//		let elem=this.state.eosElem;
//		console.log(elem);
//		console.log(this.state);
		this.state.menuCallBack('openEos');
	}
	inviteClick(){
		this.state.menuCallBack('openInvite');
	}
	hClick(fun){
		console.log(fun);
		
		
	}
	render(){

		return (
			<div>
			<div className="menu-wrap">
				<div className="menu-login flex-start">
					<div className="img-box user-pic">
						<img src={defaultUserPic}/>
					</div>
					<span className="user-name">尚未登录</span>
				</div>
				<List callBack={this.eosClick.bind(this)} key='1' theme="1" title="eos" src={eosIcon}></List>
				<List callBack={this.hClick.bind(this)} key='2' theme="1" title="shb" src={shbIcon}></List>
				<List callBack={this.inviteClick.bind(this)} key='3' theme="" title="邀请好友" src={inviteIcon}></List>
				<List callBack={this.hClick.bind(this)} key='4' theme="" title="分红" src={rnbIcon}></List>
				<List callBack={this.hClick.bind(this)} key='5' theme="" title="白皮书" src={bookIcon}></List>
				<List callBack={this.hClick.bind(this)} key='6' theme="" title="股权置换" src={stockIcon}></List>
				<List callBack={this.hClick.bind(this)} key='7' theme="" title="语言：简体中文" src={languageIcon}></List>
				<List callBack={this.hClick.bind(this)} key='8' theme="" title="玩法说明" src={ruleIcon}></List>
				<List callBack={this.hClick.bind(this)} key='9' theme="" title="添加客服" src={wxIcon}></List>
				<List callBack={this.hClick.bind(this)} key='10' theme="" title="加入电报群" src={ariIcon}></List>
		
			</div>
			</div>
			
		)
	}
}
export default Menu;