import React, { Component } from 'react';
import {canleIon} from '../../public';
import '../../style.css';
import './invite.css';
class Invite extends Component{
	constructor(props) {
    super(props);
    this.state = {

    };
    
  }
	cancel(e){
		this.props.callBack('cancelInvite');
	}
	render(){
		
		return (
			<div className="eos-recarge-wrap">
				<div className="eos-box">
					<div className='canle' onClick={this.cancel.bind(this)}>
						<img src={canleIon}/>
					</div>
					<h3 className="title">邀请好友</h3>
					<div className="input-box flex">
						<span>网&nbsp;&nbsp;址:</span>
						<span className="label label-primary">www.1234.com</span>
					</div>
					<div className="input-box flex">
						<span>邀请码:</span>
						<span className="label label-primary">115df1e2</span>
					</div>
					<button type="button" className="copy-btn btn btn-primary">一键复制</button>
					<div className="tip">邀请成功,立即获取下注额的0.5%返现</div>
					
				</div>
			</div>
		)
	}
}
export default Invite;