import React, { Component } from 'react';
import './rank.css';
import RankList from './ranklist';
class Rank extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	data:props.data?props.data:{}
    	,theme:props.theme?props.theme:1
    	
    };
    
  }
	render(){
		let rankListArr=[
			<RankList theme='1' key='1'></RankList>,
			<RankList theme='2' key='2'></RankList>,
			<RankList theme='3' key='3'></RankList>,
			<RankList theme='4' key='4'></RankList>
		];
		return(
			<div className="rank-wrap">
				<h3 className="rank-title">排行榜</h3>
				<div className="rank-th flex">
					<div>
						<span className="label label-default">排名</span>
					</div>
					<div>
						<span className="label label-default">玩家</span>
					</div>
					<div>
						<span className="label label-default">红包奖金</span>
					</div>
					<div>
						<span className="label label-default">奖励</span>
					</div>
				</div>
				{rankListArr}
			</div>
		)
	}
}

export default Rank;